import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormTitipanSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  onChange() {
    this.props.change("cashNetto", this.props.cashNetto);
    this.props.change("transferNetto", this.props.transferNetto);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-4 col-md-4">
            <Field
              name="noTitip"
              component={renderField}
              type="text"
              label="No Titip"
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="tanggal"
              label="Tanggal"
              type="date"
              component={renderField}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <label htmlFor="">Supplier</label>
            <Field name="supplier" className="form-control" component="select">
              <option value="DEFAULT" key="DEFAULT">
                silahkan pilih
              </option>
            </Field>
          </div>
          <div className="col-md-6 col-lg-6">
            <label htmlFor="">Kriteria</label>
            <Field name="supplier" className="form-control" component="select">
              <option value="DEFAULT" key="DEFAULT">
                silahkan pilih
              </option>
            </Field>
          </div>
          <div className="col-md-6 col-lg-6">
            <label htmlFor="">Supplier</label>
            <Field name="supplier" className="form-control" component="select">
              <option value="DEFAULT" key="DEFAULT">
                silahkan pilih
              </option>
            </Field>
          </div>
          <div className="col-md-6 col-lg-6">
            <Field
              name="harga"
              label="Harga"
              type="number"
              component={renderField}
              onChange={this.onChange()}
            />
          </div>
          <div className="col-lg-6 col-md-6"></div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="cash"
              label="Cash"
              type="number"
              component={renderField}
              onBlur={this.onChange()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="cashNetto"
              label="Netto"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="transfer"
              label="Transfer"
              type="number"
              component={renderField}
              onBlur={this.onChange()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="transferNetto"
              label="Netto"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="tujuan"
              label="A/C Tujuan"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <label htmlFor="">A/C Asal</label>
            <Field name="supplier" className="form-control" component="select">
              <option value="DEFAULT" key="DEFAULT">
                silahkan pilih
              </option>
            </Field>
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="tanggal"
              label="Tanggal"
              type="date"
              component={renderField}
            />
          </div>
          <div className="col-lg-3 col-md-3"></div>
          <div className="col-lg-3 col-md-3">
            <Field
              name="ciokBrutto"
              label="Ciok Bruto"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-lg-3 col-md-3">
            <Field
              name="ciokNetto"
              label="Ciok Netto"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-lg-3 col-md-3">
            <Field
              name="rongsokBruto"
              label="Rongsok Brutto"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-lg-3 col-md-3">
            <Field
              name="rongsokNetto"
              label="Rongsok Netto"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormTitipanSupplier = reduxForm({
  form: "FormTitipanSupplier",
  enableReinitialize: true,
})(FormTitipanSupplier);
const selector = formValueSelector("FormTitipanSupplier"); // <-- same as form name
export default connect((state) => {
  const { harga, cash, transfer, ciok, rongsok } = selector(
    state,
    "harga",
    "cash",
    "transfer",
    "ciok",
    "rongsok"
  );
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
  };
})(FormTitipanSupplier);
