import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import BootstrapTable from "react-bootstrap-table-next";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormTransferBalance extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "sales",
          text: "Sales",
        },
        {
          dataField: "noTransaksi",
          text: "No Transaksi",
        },
        {
          dataField: "noBon",
          text: "No Bon",
        },
        {
          dataField: "saldoGr",
          text: "Saldo (Gr)",
        },
        {
          dataField: "saldoRp",
          text: "Saldo (Rp)",
        },
        {
          dataField: "titipGr",
          text: "Titip (Gr)",
        },
        {
          dataField: "titipRp",
          text: "Titip (Rp)",
        },
      ],
      products: [
        {
          kode_salesman: "001",
          nama_salesman: "BANDUNG",
        },
        {
          kode_salesman: "002",
          nama_salesman: "BOGOR",
        },
      ],
      searchText: "",
    };
  }

  onChange() {
    this.props.change("cashNetto", this.props.cashNetto);
    this.props.change("transferNetto", this.props.transferNetto);
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  searchList() {
    console.log(this.state.searchText);
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4 col-lg-4">
            <Field
              name="tanggal"
              label="Tanggal"
              type="date"
              component={renderField}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="namaToko"
              component={renderField}
              type="text"
              label="Nama Toko"
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="namaCustomer"
              component={renderField}
              type="text"
              label="Nama Customer"
            />
          </div>
          <div className="col-lg-4 mt-5">
            <div className="form-group">
              <label htmlFor="">Cari Nomor Bon</label>
              <input
                type="text"
                id="searchNoBon"
                onChange={this.inputChange("searchText")}
                className="form-control"
              />
            </div>
          </div>
          <div className="col-lg-1 mt-5">
            <div className="form-group">
              <label htmlFor="" className="text-white">
                .
              </label>
              <button
                type="button"
                className="btn btn-primary form-control"
                onClick={() => this.searchList()}
              >
                Cari
              </button>
            </div>
          </div>
          <div className="col-md-12 col-lg-12 mt-3">
            <BootstrapTable
              keyField="noTransaksi"
              data={this.state.products}
              columns={this.state.columns}
              bordered={true}
              headerClasses="header-table"
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormTransferBalance = reduxForm({
  form: "FormTransferBalance",
  enableReinitialize: true,
})(FormTransferBalance);
const selector = formValueSelector("FormTransferBalance"); // <-- same as form name
export default connect((state) => {
  const { harga, cash, transfer, ciok, rongsok } = selector(
    state,
    "harga",
    "cash",
    "transfer",
    "ciok",
    "rongsok"
  );
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
  };
})(FormTransferBalance);
