import React, { Component } from "react";
import { Link } from "react-router-dom";
import FormTitipanSupplier from "./Component/FormTitipanSupplier";
import FormTransferBalance from "./Component/FormTransferBalance";

class TransferBalance extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12 col-d-12">
          <ol className="breadcrumb mb-2">
            <Link to="/ayugold.com/KlasifikasiCustomer">
              <button type="button" className="btn btn-secondary">
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <div className="card-body">
              <FormTransferBalance
                onSubmit={(data) => this.handlSubmit(data)}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default TransferBalance;
